/*
 * @name submit-form
 */

 // module-a
.submit-form.module-a {
	margin: 0;
	padding: 0;
	border: hidden;
	display: flex;
	flex-direction: column;
	row-gap: 16rem;

	legend {
		@include hidden;
	}

	.form-list {
		display: flex;
		flex-direction: column;
		row-gap: 16rem;

		.form-item {
			display: flex;
			flex-direction: column;

			.form-wrap {
				display: flex;

				.form-head {
					.form-name {
						display: block;
						@include font((outline: body, size: small-1x)...);
						color: color(normal, 010);
					}
				}
				.form-body {
					display: flex;
					flex: 1 1 100%;

					.form-area {
						display: flex;
						flex: 1 1 100%;

						.form:not(.flex) {
							flex-shrink: 0;
						}
						.form.flex {
							flex: 1 1 100%;
						}
					}
				}
			}
		}
	}

	&.type-c {
		.form-wrap {
			flex-direction: column;
			row-gap: 8rem;
		}
	}

	&.style-b {
		.form-list {
			padding-top: 24rem;
			border-top: 2rem solid color(normal, 020);
		}
	}
}